

















import { Component, Prop, Vue } from 'nuxt-property-decorator'

@Component({
  name: 'ConfirmDelete'
})
export default class ConfirmDelete extends Vue {
   @Prop({ type: Function, default: () => {} })
  handleDelete: any

  @Prop({ type: Boolean, default: true, required: false })
  visible!: any

  get show () {
    return this.visible
  }

  set show (value) {
    if (!value) {
      this.$emit('close')
    }
  }
}
