


























































import { Component, Prop, Vue } from 'nuxt-property-decorator'
import ConfirmDelete from '~/components/util/ConfirmDelete.vue'

@Component({
  name: 'Toolbar',
  components: {
    ConfirmDelete
  }
})
export default class Toolbar extends Vue {
  @Prop({ type: Function, default: null })
  handleBack!: any

  @Prop({ type: Function, default: null })
  handleEdit!: any

  @Prop({ type: Function, default: null })
  handleSubmit!: any

  @Prop({ type: Function, default: null })
  handleReset!: any

  @Prop({ type: Function, default: null })
  handleDelete: any

  @Prop({ type: Function, default: null })
  handleAdd!: any

  @Prop({ type: String, default: '', required: false })
  title!: any

  @Prop({ type: Boolean, default: false, required: false })
  isLoading!: any

  confirmDelete = false

  goBack () {
    if (this.handleBack) {
      this.handleBack()
    }
  }

  addItem () {
    if (this.handleAdd) {
      this.handleAdd()
    }
  }

  editItem () {
    if (this.handleEdit) {
      this.handleEdit()
    }
  }

  submitItem () {
    if (this.handleSubmit) {
      this.handleSubmit()
    }
  }

  resetItem () {
    if (this.handleReset) {
      this.handleReset()
    }
  }
}
